import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/internal/Observable';
import { startWith } from 'rxjs/internal/operators/startWith';
import { map } from 'rxjs/internal/operators/map';

import { UserService } from '../../.././api/services/user.service';
import { ApartmentService } from '../../.././api/services/apartment.service';

import { VERSION } from '@angular/material/core';

import { SharedService } from '../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

@Component({
  selector: 'app-signup-entry',
  templateUrl: './signup-entry.component.html',
  styleUrls: ['./signup-entry.component.scss']
})
export class SignupEntryComponent implements OnInit {
  version = VERSION;
//   public reactiveForm: FormGroup = new FormGroup({
//     recaptchaReactive: new FormControl(null, Validators.required)
// });
  myControl: FormControl = new FormControl();
  lstApartments:any;
  filteredOptions: Observable<string[]>;
  isApartmentSelected :boolean = false;
  user:any = {};
  lstUserType:any;
  userType:string = null;
  apartmentDetails:any;
  searchApartmentText:string = "";
  unitName:string = "";
  BlockName:string = "";
  YOUR_SITE_KEY:string = "";
  isUserSubmitted:boolean = false;
  constructor(private apartmentservice:ApartmentService, private userService:UserService,
    private cookieService:CookieService,
    private sharedService:SharedService) { 
    this.YOUR_SITE_KEY = "6LcOuyYTAAAAAHTjFuqhA52fmfJ_j5iFk5PsfXaU";
    this.loadStates();
    this.lstUserType = [{usrValue:2,usrName:"Tenant"},{usrValue:4,usrName:"Owner"}];
  }
  
  ngOnInit() {
    this.filteredOptions = this.myControl.valueChanges
    .pipe(
      startWith(''),
      map(val => this.filter(val))
    );
  }
  loadStates() {
    this.apartmentservice.getAllApartmentsPublic().subscribe((res : any) => {
       console.log(res);
       this.lstApartments = res;
    },
    error => {
      console.log(error);
    }
    );
 }
 filter(val: string): string[] {
   if(val.length>2){
    return this.lstApartments.filter(option =>
      option.apartmentName.toLowerCase().indexOf(val.toLowerCase()) === 0);
   }
}
onSelectionChanged(evt){
  this.isApartmentSelected = false;
  this.unitName = this.BlockName = this.userType = "";
  this.user = {};
  this.apartmentDetails = {};
  this.apartmentservice.getAllApartmentsPublic().subscribe((res : any) => {
   this.apartmentDetails = res.filter(itm=>itm.apartmentName.toLowerCase() === evt.option.value.toLowerCase())[0];
   console.log(this.apartmentDetails);
   this.isApartmentSelected = true;
   this.searchApartmentText = "";
 },
 error => {
   console.log(error);
 }
 );
  console.log(evt);
  console.log(evt.option.value);
}
cancelSignup(){
  this.isApartmentSelected = false;
}
submitAddResidentForm(form) {
  var notesStrVal  =`{"UserType":"${this.userType}","BlockName":"${this.BlockName}","UnitName":"${this.unitName}"}`;
  this.isUserSubmitted = true;
  console.log(this.apartmentDetails);
  console.log(notesStrVal);
    //add user
    let userDetails = {
  "apartmentId": parseInt(this.apartmentDetails.apartmentId),
  "firstName":this.user.firstName,
  "lastName": this.user.lastName,
  "emailId": this.user.emailId,
  "genderName": this.user.genderId.toString(),
  "phoneNumber": this.user.phoneNumber.toString(),
  "notes": notesStrVal,
  "comments": "",
  "isActive": true,
  "insertedOn": moment().format(),
  "updatedOn": "",
  "signupSubNotes": [
    {
      "signupSubNotesId": 0,
      "signupUserRequestId": 0,
      "userType": this.userType,
      "blockUnit": this.BlockName,
      "unit": this.unitName,
      "genderName": this.user.genderId.toString()
    }
  ]
}
    console.log(userDetails);
    //add user  (addSignUpUser)
    this.userService.addSignupUserRequestResponse(userDetails).subscribe((res:any) => {
          console.log(res);
          if(res.message) {
            setTimeout(() => {
              this.sharedService.setAlertMessage("Signup has been created successfull.");
            }, 200);
            this.isApartmentSelected = false; this.unitName = this.BlockName = this.userType = "";
            this.user = {};
            this.searchApartmentText = "";
          }
          else {
            this.isUserSubmitted = false;
          }

    },
    error => {
      console.log(error);
    }
    );


  
}

}
